import React from 'react';
import TextField from '@material-ui/core/TextField';

const InputForm = (props) => {
  const {
    id, type, label, change, value, name, className,
  } = props;

  return (
    <TextField
      id={id}
      name={name}
      label={label}
      type={type}
      margin="normal"
      onChange={change}
      value={value}
      variant="outlined"
      className={className}
    />
  );
};

export default InputForm;
