import React, { Component } from 'react';

import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';

import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { KeyboardDatePicker } from '@material-ui/pickers';

import Input from '../../components/input';
import api from '../../services/api';

const styles = theme => ({
  card: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  center: {
    display: 'flex',
    justifyContent: 'flex-start',
  },

  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  fullWidth: {
    width: '100%',
  },
  button: {
    margin: theme.spacing(1),
    marginLeft: 'auto',
  },
});

class Cursos extends Component {
  state = {
    nome_Curso: '',
    descricao: '',
    data_Inicio: null,
    data_Fim: null,
    valor: '',
    numero_Maximo_Alunos: 0,
    alunos: [],
    aprovado: false,
  };

  componentWillMount() {
    document.title = 'Abel Santana - Cursos';
    const token = localStorage.getItem('authToken');

    api
      .get('/api/cursos/', {
        headers: {
          Authorization: `Token ${token}`,
        },
      })
      .then((response) => {
        console.log(response);
        this.setState({ ...response.data[0] });
        console.log(this.state);
      })
      .catch(() => {});
  }

  handleDateChange = (date) => {
    this.setState({ data_Inicio: date });
  };

  render() {
    const { data_Inicio } = this.state;

    return (
      <Card>
        <CardContent>
          <Typography
            gutterBottom
            variant="h3"
            component="h2"
            style={{ display: 'flex', justifyContent: 'center' }}
          >
            Cursos
          </Typography>

          <Grid container spacing={4}>
            {['Inbox', 'Starred', 'Send email', 'Drafts'].map((text, index) => (
              <Grid item xl={4} xs={12} lg={4} md={6} sm={12}>
                <Card style={{ background: '#A3142B' }}>
                  <CardContent>
                    <Typography
                      gutterBottom
                      variant="h4"
                      component="h3"
                      style={{ display: 'flex', justifyContent: 'center' }}
                    >
                      Nome do Curso
                    </Typography>
                    Vagas: 25/35 Data Inicio: 10/05/2019 Data Fim: 10/08/2019
                    <CardActions>
                      <Button variant="contained" color="primary" type="submit">
                        Saiba Mais
                      </Button>
                    </CardActions>
                  </CardContent>
                </Card>
              </Grid>
            ))}
          </Grid>
        </CardContent>
      </Card>
    );
  }
}

export default Cursos;
