/* eslint-disable camelcase */
import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import MaskedInput from 'react-text-mask';
import { KeyboardDatePicker } from '@material-ui/pickers';

import Input from '../../components/input';
import api from '../../services/api';

const styles = theme => ({
  card: {
    minWidth: 275,
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  center: {
    display: 'flex',
    justifyContent: 'flex-start',
  },

  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  fullWidth: {
    width: '100%',
  },
  button: {
    margin: theme.spacing(1),
    marginLeft: 'auto',
  },
});

function TextMaskCustom(props) {
  const { inputRef, ...other } = props;

  return (
    <MaskedInput
      {...other}
      ref={(ref) => {
        inputRef(ref ? ref.inputElement : null);
      }}
      mask={[
        '(',
        /[0-9]/,
        /\d/,
        /\d/,
        ')',
        ' ',
        /\d/,
        /\d/,
        /\d/,
        /\d/,
        /\d/,
        '-',
        /\d/,
        /\d/,
        /\d/,
        /\d/,
      ]}
      placeholderChar={'\u2000'}
      showMask
    />
  );
}

function CepMaskCustom(props) {
  const { inputRef, ...other } = props;

  return (
    <MaskedInput
      {...other}
      ref={(ref) => {
        inputRef(ref ? ref.inputElement : null);
      }}
      mask={[/[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/, /[0-9]/]}
      placeholderChar={'\u2000'}
      showMask
    />
  );
}

class Cursos extends Component {
  state = {
    nome_completo: '',
    email: '',
    email_responsavel: '',
    telefone_residencial: '(0  )    -    ',
    telefone_comercial: '(0  )    -    ',
    celular: '(0  )    -    ',
    cep: '',
    logradouro: '',
    estado: '',
    endereco: '',
    cidade: '',
    complemento: '',
    data: null,
    sexo: '',
    filiacao: '',
    experiencia_teatro: '',
    como_ficou_sabendo: '',
  };

  componentWillMount() {
    document.title = 'Abel Santana - Perfil';
    const token = localStorage.getItem('authToken');

    api
      .get('/api/cadastros/', {
        headers: {
          Authorization: `Token ${token}`,
        },
      })
      .then((response) => {
        this.setState({ ...response.data[0] });
      })
      .catch(() => {});
  }

  handleChange = name => (event) => {
    this.setState({
      [name]: event.target.value,
    });
  };

  handleDateChange = (date) => {
    this.setState({ data: date });
  };

  handleSubmit = (event) => {
    const token = localStorage.getItem('authToken');

    event.preventDefault();
    const data = this.state;
    api.post(`/api/cadastroAluno/${data.usuario}`, data, {
      headers: {
        Authorization: `Token ${token}`,
      },
    });
  };

  render() {
    const { classes } = this.props;
    const {
      nome_completo,
      email,
      email_responsavel,
      telefone_residencial,
      telefone_comercial,
      celular,
      cep,
      logradouro,
      endereco,
      estado,
      cidade,
      complemento,
      data,
      sexo,
      filiacao,
      experiencia_teatro,
      como_ficou_sabendo,
    } = this.state;
    return (
      <Card className={classes.card}>
        <form onSubmit={this.handleSubmit}>
          <CardContent>
            <Typography gutterBottom variant="h3" component="h2" className={classes.center}>
              Meu Perfil
            </Typography>
            <div className={classes.root}>
              <Grid container spacing={4}>
                <Grid item xl={4} xs={12} lg={4} md={6} sm={12}>
                  <Input
                    id="nomeCompleto"
                    type="text"
                    label="Nome Completo"
                    value={nome_completo}
                    className={classes.fullWidth}
                    change={this.handleChange('nome_completo')}
                  />
                </Grid>
                <Grid item xl={4} xs={12} lg={4} md={6} sm={12}>
                  <Input
                    id="email"
                    type="email"
                    label="Email"
                    value={email}
                    className={classes.fullWidth}
                    change={this.handleChange('email')}
                  />
                </Grid>
                <Grid item xl={4} xs={12} lg={4} md={12} sm={12}>
                  <Input
                    id="email"
                    type="email"
                    label="Email do Responsavel"
                    value={email_responsavel}
                    className={classes.fullWidth}
                    change={this.handleChange('email_responsavel')}
                  />
                </Grid>
              </Grid>

              <Grid container spacing={4}>
                <Grid item xl={4} xs={12} lg={4} md={6}>
                  <TextField
                    id="telefoneResidencial"
                    label="Telefone Residencial"
                    fullWidth
                    onChange={this.handleChange('telefone_residencial')}
                    InputProps={{
                      inputComponent: TextMaskCustom,
                    }}
                    margin="normal"
                    variant="outlined"
                    value={telefone_residencial}
                  />
                </Grid>
                <Grid item xl={4} xs={12} lg={4} md={6}>
                  <TextField
                    id="telefoneComercial"
                    fullWidth
                    label="Telefone Comercial"
                    onChange={this.handleChange('telefone_comercial')}
                    InputProps={{
                      inputComponent: TextMaskCustom,
                    }}
                    margin="normal"
                    variant="outlined"
                    value={telefone_comercial}
                  />
                </Grid>
                <Grid item xl={4} xs={12} lg={4} md={12}>
                  <TextField
                    id="celular"
                    fullWidth
                    label="Celular"
                    onChange={this.handleChange('celular')}
                    InputProps={{
                      inputComponent: TextMaskCustom,
                    }}
                    margin="normal"
                    variant="outlined"
                    value={celular}
                  />
                </Grid>
              </Grid>
              <Grid container spacing={4}>
                <Grid item xl={3} xs={12} lg={4}>
                  <Input
                    id="logradouro"
                    type="text"
                    label="Logradouro"
                    value={logradouro}
                    className={classes.fullWidth}
                    change={this.handleChange('logradouro')}
                  />
                </Grid>
                <Grid item xl={3} xs={12} lg={4}>
                  <Input
                    id="endereco"
                    type="text"
                    label="Endereço"
                    value={endereco}
                    className={classes.fullWidth}
                    change={this.handleChange('endereco')}
                  />
                </Grid>
                <Grid item xl={2} xs={12} lg={4}>
                  <Input
                    id="complemento"
                    type="text"
                    label="Complemento"
                    value={complemento}
                    className={classes.fullWidth}
                    change={this.handleChange('complemento')}
                  />
                </Grid>
                <Grid item xl={2} xs={12} lg={4} md={4}>
                  <Input
                    id="cidade"
                    type="text"
                    label="Cidade"
                    value={cidade}
                    className={classes.fullWidth}
                    change={this.handleChange('cidade')}
                  />
                </Grid>
                <Grid item xl={1} xs={12} lg={4} md={4}>
                  <Input
                    id="estado"
                    type="text"
                    label="Estado"
                    value={estado}
                    className={classes.fullWidth}
                    change={this.handleChange('estado')}
                  />
                </Grid>

                <Grid item xl={1} xs={12} lg={4} md={4}>
                  <TextField
                    id="cep"
                    fullWidth
                    label="CEP"
                    onChange={this.handleChange('cep')}
                    InputProps={{
                      inputComponent: CepMaskCustom,
                    }}
                    margin="normal"
                    variant="outlined"
                    value={cep}
                  />
                </Grid>
              </Grid>
              <Grid container spacing={5}>
                <Grid item xl={2} xs={12} lg={4} md={6}>
                  <KeyboardDatePicker
                    variant="inline"
                    inputVariant="outlined"
                    label="Data Nascimento"
                    format="dd/MM/yyyy"
                    margin="normal"
                    value={data}
                    invalidDateMessage="Data Invalida"
                    InputAdornmentProps={{ position: 'end' }}
                    onChange={date => this.handleDateChange(date)}
                  />
                </Grid>
                <Grid item xl={2} xs={12} lg={4} md={6}>
                  <Input
                    id="sexo"
                    type="text"
                    label="Sexo"
                    value={sexo}
                    className={classes.fullWidth}
                    change={this.handleChange('sexo')}
                  />
                </Grid>
                <Grid item xl={2} xs={12} lg={4}>
                  <Input
                    id="filiacao"
                    type="text"
                    label="Filiação"
                    value={filiacao}
                    className={classes.fullWidth}
                    change={this.handleChange('filiacao')}
                  />
                </Grid>

                <Grid item xl={2} xs={12} lg={6}>
                  <Input
                    id="experiencia"
                    type="text"
                    label="Experiencia no teatro ?"
                    value={experiencia_teatro}
                    className={classes.fullWidth}
                    change={this.handleChange('experiencia_teatro')}
                  />
                </Grid>
                <Grid item xl={4} xs={12} lg={6}>
                  <Input
                    id="comoficousabendo"
                    type="text"
                    label="Como ficou sabendo ?"
                    value={como_ficou_sabendo}
                    className={classes.fullWidth}
                    change={this.handleChange('como_ficou_sabendo')}
                  />
                </Grid>
              </Grid>
            </div>
          </CardContent>
          <CardActions>
            <Button variant="contained" color="primary" className={classes.button} type="submit">
              Salvar
            </Button>
          </CardActions>
        </form>
      </Card>
    );
  }
}

export default withStyles(styles)(Cursos);
